import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar, MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TextControl, ToggleControl, SelectControl, __experimentalUnitControl as UnitControl, Button, ToolbarGroup, ToolbarButton, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import BtnGroup from '../../Components/BtnGroup';

import options from './Const/options';
const { sourceTypes, loadingTypes, pxUnit, perUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes }) => {
	const { alignment, sourceType, model, modelLink, width, height, isTouchMove, isZoom, loadingType } = attributes;

	const onModelSelect = ({ id, url, title }) => setAttributes({ model: { ...model, id, url, title } });

	return <>
		<InspectorControls>
			<PanelBody className='bPlPanelBody help' title={__('Help', '3d-viewer-block')}>
				<div className='helpItem'>
					<a href='https://bblockswp.com/docs/3d-viewer-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', '3d-viewer-block')}</a>
				</div>

				<div className='helpItem rateUs'>
					<a href='https://wordpress.org/support/plugin/3d-viewer-block/reviews/#new-post' target='_blank' rel='noreferrer'>
						<span><Dashicon icon='star-filled' />{__('Would you please rate us?', '3d-viewer-block')}</span>
						<span>{__('We are new and we need your help to grow!üôè', '3d-viewer-block')}</span>
					</a>
				</div>
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('3D Model Settings', '3d-viewer-block')}>
				<PanelRow>
					<Title mt='0'>{__('Source Type:', '3d-viewer-block')}</Title>
					<BtnGroup value={sourceType} onChange={val => setAttributes({ sourceType: val })} options={sourceTypes} />
				</PanelRow>
				{'upload' === sourceType && <small>{__('Please refresh the page first, if you install the plugin for the first time from gutenberg editor')}</small>}

				{'upload' === sourceType && <>
					<Title>{__('Upload:', '3d-viewer-block')}</Title>

					<div className='editImageHolder'>
						<div className='mediaControl'>
							<MediaUpload allowedTypes={['model/gltf-binary']} value={model?.id} onSelect={onModelSelect} render={({ open }) =>
								!model.url ? <div className='btnControl'>
									<Button icon='upload' onClick={open}>{__('Upload', '3d-viewer-block')}</Button>
								</div> : <div className='btnControl'>
									<Button icon='controls-repeat' onClick={open}>{__('Replace', '3d-viewer-block')}</Button>
									<Button icon='exit' onClick={() => setAttributes({ model: { id: null, url: '', title: '' } })} className='btnRed'>{__('Remove', '3d-viewer-block')}</Button>
								</div>} />
						</div>

						<model-viewer class='modelPreview' src={model?.url} alt={model?.title}></model-viewer>
					</div>
				</>}

				{'link' === sourceType && <>
					<Title>{__('Model Link:', '3d-viewer-block')}</Title>
					<TextControl value={modelLink} onChange={val => setAttributes({ modelLink: val })} />
				</>}

				<UnitControl className='mt20' label={__('Width:', '3d-viewer-block')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit, perUnit, emUnit]} />
				<small>{__('Keep width 0, to auto width.', '3d-viewer-block')}</small>

				<UnitControl className='mt20' label={__('Height:', '3d-viewer-block')} labelPosition='left' value={height} onChange={val => setAttributes({ height: val })} units={[pxUnit, perUnit, emUnit]} />
				<small>{__('Keep height 0, to 350px height.', '3d-viewer-block')}</small>

				<ToggleControl className='mt20' label={__('Controls via mouse/touch', '3d-viewer-block')} checked={isTouchMove} onChange={val => setAttributes({ isTouchMove: val })} />

				{isTouchMove && <>
					<ToggleControl className='mt20' label={__('Zoom', '3d-viewer-block')} checked={isZoom} onChange={val => setAttributes({ isZoom: val })} />
					<small>{__(`Enable 'Controls via mouse/touch' to work Zoom!`, '3d-viewer-block')}</small>
				</>}

				<PanelRow className='mt20'>
					<Title mt='0' mb='0'>{__('Loading Type:', 'b-blocks')}</Title>
					<SelectControl value={loadingType} onChange={val => setAttributes({ loadingType: val })} options={loadingTypes} />
				</PanelRow>
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			{model?.url && <ToolbarGroup className='bPlToolbar'>
				<MediaUploadCheck>
					<MediaUpload allowedTypes={['model/gltf-binary']} value={model?.id} onSelect={onModelSelect} render={({ open }) => <ToolbarButton label={__('Edit 3D Model:', '3d-viewer-block')} icon='edit' onClick={open} />} />
				</MediaUploadCheck>
			</ToolbarGroup>}

			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('3D Model Alignment')} alignmentControls={[
				{ title: __('3D Model in left', '3d-viewer-block'), align: 'left', icon: 'align-left' },
				{ title: __('3D Model in center', '3d-viewer-block'), align: 'center', icon: 'align-center' },
				{ title: __('3D Model in right', '3d-viewer-block'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>
	</>;
};
export default Settings;